<template>
  <section class="modal">
    <loading v-if="loading" />
    <div
      class="modal-container bg-white flex flex-col md:flex-row flex-wrap"
      v-if="product"
    >
      <span class="close" @click="closeModal">
        <font-awesome-icon :icon="['fas', 'times']"></font-awesome-icon>
      </span>
      <div
        class="product-img flex justify-center mb-0 sm:mb-11 items-center md:mb-0 w-full md:w-5/12"
        v-if="product.image"
      >
        <img class="w-full" :src="product.image" alt="" />
      </div>
      <div class="product-details self-center p-4 w-full md:w-7/12">
        <div class="title">
          <h3 class="text-lg md:text-xl lg:text-2xl font-bold mb-5">
            {{ product.title }}
          </h3>
        </div>
        <div class="quantity font-bold text-lg mb-4">
          <span>{{ product.price }} $</span>
        </div>
        <div class="desc mb-4">
          <p class="text-md">
            {{ product.description }}
          </p>
        </div>
        <div class="rate flex justify-center">
          <span>
            <font-awesome-icon
              :icon="[product.rating.rate > 1 ? 'fa' : 'far', 'star']"
              class="text-secondary"
            ></font-awesome-icon>
          </span>
          <span>
            <font-awesome-icon
              class="text-secondary"
              :icon="[product.rating.rate > 2 ? 'fa' : 'far', 'star']"
            ></font-awesome-icon>
          </span>
          <span>
            <font-awesome-icon
              class="text-secondary"
              :icon="[product.rating.rate > 3 ? 'fa' : 'far', 'star']"
            ></font-awesome-icon>
          </span>
          <span>
            <font-awesome-icon
              class="text-secondary"
              :icon="[product.rating.rate > 4 ? 'fa' : 'far', 'star']"
            ></font-awesome-icon>
          </span>
          <span>
            <font-awesome-icon
              class="text-secondary"
              :icon="[product.rating.rate > 4 ? 'fa' : 'far', 'star']"
            ></font-awesome-icon>
          </span>
          <span class="px-2 text-sm">{{ product.rating.rate }}</span>
        </div>
        <div v-if="productTotal">
          {{ productTotal }}
        </div>
        <div class="action flex justify-center w-full my-4">
          <button
            @click="addToCart()"
            class="px-3 py-2 mx-2 bg-secondary font-bold text-md text-white rounded-lg"
          >
            Add to cart
          </button>
          <button
            @click="removeFromCart()"
            class="px-3 py-2 mx-2 bg-secondary font-bold text-md text-white rounded-lg"
          >
            remove
          </button>
          <button
            class="px-3 py-2 mx-2 bg-secondary font-bold text-md text-white rounded-lg"
          >
            Buy now
          </button>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import Loading from "./Loading.vue";
export default {
  props: ["productId"],
  components: { Loading },
  data() {
    return {
      product: null,
      loading: null,
    };
  },
  mounted() {
    this.getProduct();
  },
  methods: {
    closeModal() {
      this.$emit("close-modal");
    },
    addToCart() {
      this.$store.commit("addToCart", this.product);
    },
    removeFromCart() {
      this.$store.commit("removeFromCart", this.product);
    },
    getProduct() {
      this.loading = true;
      this.axios.get(`products/${this.productId}`).then((data) => {
        this.product = data.data;
        this.loading = false;
      });
    },
  },
  computed: {
    productTotal() {
      return this.$store.getters.productQuantity(this.product);
    },
  },
};
</script>

