<template>
  <section class="py-5 bg-primary">
    <div class="nav container mx-auto items-center flex justify-between">
      <div class="brand w-1/4">
        <router-link class="text-3xl font-bold" :to="{ name: 'Home' }"
          >MyShop</router-link
        >
      </div>

      <Search class="w-1/2" v-show="showSearch" />
      <div
        class="cart-container relative flex justify-evenly w-1/2 md:w-1/4 items-center"
      >
        <div v-show="showSearchIcon">
          <font-awesome-icon
            class="text-2xl cursor-pointer"
            :icon="['fas', 'search']"
            @click="show"
          ></font-awesome-icon>
        </div>
        <router-link
          class="cart flex items-center justify-center"
          :to="{ name: 'Cart' }"
        >
          <div class="cart-ic text-white">
            {{ cartCont || 0 }}
          </div>
          <font-awesome-icon
            class="text-2xl"
            :icon="['fas', 'shopping-cart']"
          ></font-awesome-icon>
        </router-link>
        <p
          class="cursor-pointer flex items-center bg-secondary font-bold text-black justify-center rounded-full"
          style="width: 40px; height: 40px"
          @click="profileInfo = !profileInfo"
          v-if="isLoggedIn"
        >
          {{ this.$store.state.profileInitials }}
        </p>
        <div
          class="profile-info flex flex-col bg-white rounded-lg z-30"
          v-show="profileInfo"
        >
          <div
            class="info flex justify-between items-center border-b border-secondary p-4 pb-4"
          >
            <div class="pr-5">
              <p
                class="bg-secondary flex items-center justify-center font-bold text-black rounded-full"
                style="width: 40px; height: 40px"
              >
                {{ this.$store.state.profileInitials }}
              </p>
            </div>
            <div class="text-left text-lg font-bold">
              <p class="pb-4">
                {{ this.$store.state.firstName }}
                {{ this.$store.state.lastName }}
              </p>
              <p>{{ this.$store.state.email }}</p>
            </div>
          </div>
          <div class="action pt-4 px-4" @click="profileInfo = !profileInfo">
            <router-link class="flex" :to="{ name: 'profile' }">
              <font-awesome-icon
                class="icon text-2xl mr-4"
                :icon="['fas', 'user-alt']"
              ></font-awesome-icon>
              <p class="text-xl font-bold">Profile</p>
            </router-link>
            <p
              class="cursor-pointer my-2 text-left text-xl font-bold"
              @click="logout"
            >
              <font-awesome-icon
                class="icon text-2xl mr-4"
                :icon="['fas', 'sign-out-alt']"
              ></font-awesome-icon
              >Sign out
            </p>
          </div>
        </div>
        <div v-if="!isLoggedIn" class="text-sm md:text-lg text-block font-bold">
          <router-link :to="{ name: 'Login' }">Login</router-link>
          <span class="px-2">/</span>
          <router-link :to="{ name: 'Register' }">Register</router-link>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import Search from "../components/Search.vue";
import firebase from "firebase/app";
import "firebase/auth";
export default {
  data() {
    return {
      showSearch: null,
      showSearchIcon: null,
      windowWidth: null,
      profileInfo: false,
    };
  },
  components: { Search },
  created() {
    window.addEventListener("resize", this.checkWidth);
    this.checkWidth();
  },
  computed: {
    cartCont() {
      return this.$store.getters.getCartCount;
    },
    isLoggedIn() {
      return this.$store.getters.isAuthenticated;
    },
  },
  methods: {
    show() {
      this.showSearch = !this.showSearch;
    },
    logout() {
      firebase.auth().signOut();
    },
    checkWidth() {
      this.windowWidth = window.innerWidth;
      if (this.windowWidth < 768) {
        this.showSearch = false;
        this.showSearchIcon = true;
        return;
      }
      this.showSearch = true;
      this.showSearchIcon = false;
      return;
    },
  },
};
</script>

<style lang="scss">
.cart-container {
  .cart {
    width: 50px;
    height: 50px;
    position: relative;
    .cart-ic {
      position: absolute;
      font-size: 10px;
      top: 0;
      width: 20px;
      height: 20px;
      right: 0;

      @apply bg-secondary flex justify-center items-center font-bold rounded-full;
    }
  }
}
.profile-info {
  position: absolute;
  top: 60px;
  right: 0;
  box-shadow: 0 0 5px 0;
}
</style>
