<template>
  <section>
    <div
      class="xs:container 2xs:flex-row flex flex-col-reverse lg:w-1/2 md:w-3/4 w-full md:mx-auto items-center"
    >
      <div
        class="flex items-center xs:justify-between rounded-full rounded-l-lg, border-2 border-primary border-opacity-20 w-full px-3 bg-white"
      >
        <div
          class="flex justify-around items-center rounded-full md:w-1/4 w-1/3"
        >
          <button
            class="search xs:mt-0 mt-2 flex px-2"
            :class="searchInput ? 'cursor-pointer' : 'cursor-not-allowed'"
            @click="search()"
            :disabled="searchInput ? false : true"
          >
            Search
          </button>
          <span class="text-primary text-lg">|</span>
        </div>
        <div class="flex mx-4 md:w-3/4 w-2/3">
          <input
            class="placeholder-secondary px-3 w-full bg-transparent xs:text-base text-sm"
            v-model="searchInput"
            placeholder="Word to search"
            @keypress.enter="search()"
          />
        </div>
        <font-awesome-icon
          :icon="['fas', 'search']"
          @click="search()"
          :class="searchInput ? 'cursor-pointer' : 'cursor-not-allowed'"
        ></font-awesome-icon>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      searchInput: "",
    };
  },
  methods: {
    search() {
      this.$router.push({
        path: "/search",
        query: {
          ...this.$route.query,
          sort: this.searchInput,
        },
      });
    },

    handelerSearchInput() {
      this.$route.path === "/search"
        ? (this.searchInput = this.$route.query.sort)
        : (this.searchInput = "");
    },
  },
  watch: {
    $route() {
      this.handelerSearchInput();
    },
  },
};
</script>

