<template>
  <section>
    <loading v-if="loading" />
    <modal :errMsg="errMsg" v-if="modalActive" v-on:close-modal="closeModal" />
    <div class="form-wrapper flex justify-center items-center">
      <form class="login-inputs flex flex-col">
        <div class="input">
          <input
            class="w-full pl-10 py-2 px-1 mb-11 border-b border-black focus:outline-none"
            type="text"
            required
            v-model="firstname"
            placeholder="First Name"
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['fas', 'user-alt']"
          ></font-awesome-icon>
        </div>
        <div class="input">
          <input
            class="w-full pl-10 py-2 px-1 mb-11 border-b border-black focus:outline-none"
            type="text"
            v-model="lastname"
            required
            placeholder="Last Name"
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['fas', 'user-alt']"
          ></font-awesome-icon>
        </div>
        <div class="input">
          <input
            class="w-full pl-10 py-2 px-1 mb-11 border-b border-black focus:outline-none"
            type="email"
            v-model="email"
            placeholder="Email"
            disabled
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['far', 'envelope']"
          ></font-awesome-icon>
        </div>
        <div class="input">
          <input
            class="w-full pl-10 py-2 mb-2 px-1 border-b border-black focus:outline-none"
            type="text"
            v-model="streetName"
            placeholder="Street Name"
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['fas', 'map-marker-alt']"
          ></font-awesome-icon>
        </div>
        <div class="input">
          <input
            class="w-full pl-10 py-2 mb-2 px-1 border-b border-black focus:outline-none"
            type="text"
            v-model="city"
            placeholder="City name"
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['fas', 'map-marker-alt']"
          ></font-awesome-icon>
        </div>
        <div class="input">
          <input
            class="w-full pl-10 py-2 mb-2 px-1 border-b border-black focus:outline-none"
            type="text"
            v-model="state"
            placeholder="State"
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['fas', 'map-marker-alt']"
          ></font-awesome-icon>
        </div>

        <div class="flex">
          <button
            class="mt-11 bg-secondary text-lg text-black font-bold px-5 rounded-lg py-3"
            @click.prevent="update"
          >
            Update
          </button>
        </div>
      </form>
    </div>
  </section>
</template>

<script>
import Loading from "../components/Loading.vue";
import Modal from "./Modal.vue";
export default {
  components: { Loading, Modal },
  data() {
    return {
      err: false,
      errMsg: "",
      loading: null,
      modalActive: null,
    };
  },
  methods: {
    update() {
      this.$store.dispatch("updateUserSettings");
      this.modalActive = !this.modalActive;
      this.errMsg = "Changes Completed";
    },
    closeModal() {
      this.modalActive = !this.modalActive;
    },
  },
  computed: {
    firstname: {
      get() {
        return this.$store.state.firstName;
      },
      set(payload) {
        this.$store.commit("changeFirstName", payload);
      },
    },
    lastname: {
      get() {
        return this.$store.state.lastName;
      },
      set(payload) {
        this.$store.commit("changeLastName", payload);
      },
    },
    city: {
      get() {
        return this.$store.state.city;
      },
      set(payload) {
        this.$store.commit("changeCity", payload);
      },
    },
    state: {
      get() {
        return this.$store.state.state;
      },
      set(payload) {
        this.$store.commit("changeState", payload);
      },
    },
    email() {
      return this.$store.state.email;
    },
    streetName: {
      get() {
        return this.$store.state.streetName;
      },
      set(payload) {
        this.$store.commit("changeStreetName", payload);
      },
    },
  },
};
</script>

<style lang="scss" scoped>
.form-wrapper {
  min-height: calc(100vh - 180px);
  .login-inputs {
    width: 100%;
    max-width: 350px;
    .input {
      position: relative;
      .icon {
        position: absolute;
        top: 5px;
        left: 5px;
      }
    }
  }
}
</style>
