<template>
  <section>
    <loading v-if="loading" />
    <div class="form-wrapper flex justify-center items-center">
      <form class="login-inputs flex flex-col">
        <p class="mb-20 text-lg">
          have an account?
          <router-link class="font-bold text-secondary" :to="{ name: 'login' }">
            Login
          </router-link>
        </p>

        <div class="input">
          <input
            class="w-full pl-10 py-2 px-1 mb-11 border-b border-black focus:outline-none"
            type="text"
            required
            v-model="firstname"
            placeholder="First Name"
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['fas', 'user-alt']"
          ></font-awesome-icon>
        </div>
        <div class="input">
          <input
            class="w-full pl-10 py-2 px-1 mb-11 border-b border-black focus:outline-none"
            type="text"
            v-model="lastname"
            required
            placeholder="Last Name"
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['fas', 'user-alt']"
          ></font-awesome-icon>
        </div>
        <div class="input">
          <input
            class="w-full pl-10 py-2 px-1 mb-11 border-b border-black focus:outline-none"
            type="email"
            v-model="email"
            placeholder="Email"
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['far', 'envelope']"
          ></font-awesome-icon>
        </div>
        <div class="input">
          <input
            class="w-full pl-10 py-2 px-1 mb-11 border-b border-black focus:outline-none"
            type="password"
            v-model="password"
            placeholder="Enter Your password"
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['fas', 'lock']"
          ></font-awesome-icon>
        </div>
        <div class="input">
          <input
            class="w-full pl-10 py-2 mb-2 px-1 border-b border-black focus:outline-none"
            type="text"
            v-model="streetName"
            placeholder="Street Name"
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['fas', 'map-marker-alt']"
          ></font-awesome-icon>
        </div>
        <div class="input">
          <input
            class="w-full pl-10 py-2 mb-2 px-1 border-b border-black focus:outline-none"
            type="text"
            v-model="city"
            placeholder="City name"
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['fas', 'map-marker-alt']"
          ></font-awesome-icon>
        </div>
        <div class="input">
          <input
            class="w-full pl-10 py-2 mb-2 px-1 border-b border-black focus:outline-none"
            type="text"
            v-model="state"
            placeholder="State"
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['fas', 'map-marker-alt']"
          ></font-awesome-icon>
        </div>

        <p v-show="err" class="text-red-500 font-bold">{{ errMsg }}</p>
        <router-link
          class="flex justify-end text-sm"
          :to="{ name: 'ResetPassword' }"
          >Forget Password</router-link
        >
        <div class="flex">
          <button
            class="mt-11 bg-secondary text-lg text-black font-bold px-5 rounded-lg py-3"
            @click.prevent="register"
          >
            Register
          </button>
        </div>
      </form>
    </div>
  </section>
</template>

<script>
import firebase from "firebase/app";
import "firebase/auth";
import db from "../firebase/firebaseInit";
import Loading from "../components/Loading.vue";
export default {
  components: { Loading },
  data() {
    return {
      firstname: "",
      lastname: "",
      email: "",
      password: "",
      streetName: "",
      state: "",
      city: "",
      err: false,
      errMsg: "",
      loading: null,
    };
  },
  methods: {
    async register() {
      if (
        this.firstname !== "" &&
        this.lastname !== "" &&
        this.email !== "" &&
        this.password !== "" &&
        this.streetName !== "" &&
        this.city !== "" &&
        this.state !== ""
      ) {
        this.err = false;
        this.errMsg = "";
        const firebaseAuth = await firebase.auth();
        const createUser = await firebaseAuth.createUserWithEmailAndPassword(
          this.email,
          this.password
        );
        const result = await createUser;
        const database = await db.collection("users").doc(result.user.uid);
        await database.set({
          firstname: this.firstname,
          lastname: this.lastname,
          email: this.email,
          state: this.state,
          city: this.city,
          streetName: this.streetName,
          cart: [],
          orderPlaced: [],
        });
        this.$router.push({ name: "Home" });
        // return;
      } else {
        this.err = true;
        this.errMsg = "Please Fill all field";
      }
    },
  },
};
</script>

