.form-wrapper {
  min-height: calc(100vh - 180px);
  .login-inputs {
    width: 100%;
    max-width: 350px;
    .input {
      position: relative;
      .icon {
        position: absolute;
        top: 5px;
        left: 5px;
      }
    }
  }
}
