<template>
  <section>
    <div class="form-wrapper flex justify-center items-center">
      <form class="login-inputs flex flex-col">
        <p class="mb-20 text-lg">
          Don't have an account?
          <router-link
            class="font-bold text-secondary"
            :to="{ name: 'Register' }"
          >
            Register
          </router-link>
        </p>

        <div class="input">
          <input
            class="w-full pl-10 py-2 px-1 mb-11 border-b border-black focus:outline-none"
            type="email"
            v-model="email"
            placeholder="Email"
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['far', 'envelope']"
          ></font-awesome-icon>
        </div>
        <div class="input">
          <input
            class="w-full pl-10 py-2 mb-2 px-1 border-b border-black focus:outline-none"
            type="password"
            v-model="password"
            placeholder="Passwrod"
          />
          <font-awesome-icon
            class="icon text-2xl"
            :icon="['fas', 'lock']"
          ></font-awesome-icon>
        </div>

        <p v-show="err" class="text-red-500 font-bold">{{ errMsg }}</p>
        <router-link
          class="flex justify-end text-sm"
          :to="{ name: 'ResetPassword' }"
          >Forget Password</router-link
        >
        <div class="flex">
          <button
            class="mt-11 bg-secondary text-lg text-black font-bold px-5 rounded-lg py-3"
            @click.prevent="login"
          >
            Login
          </button>
        </div>
      </form>
    </div>
  </section>
</template>

<script>
import firebase from "firebase/app";
// import db from "../firebase/firebaseInit";
import "firebase/auth";
export default {
  name: "Login",
  data() {
    return {
      email: "",
      password: "",
      err: false,
      errMsg: "",
      cart: [],
    };
  },
  methods: {
    login() {
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(() => {
          this.err = false;
          this.errMsg = "";
          // const cart = localStorage.getItem("cart");

          this.$router.push({ name: "Home" });
        })
        .catch((err) => {
          this.err = true;
          this.errMsg = err.message;
        })
        .finally(async () => {
          if (localStorage.getItem("cart")) {
            // console.log("true");
            this.cart = JSON.parse(localStorage.getItem("cart"));
          }
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.form-wrapper {
  min-height: calc(100vh - 180px);
  .login-inputs {
    width: 100%;
    max-width: 350px;
    .input {
      position: relative;
      .icon {
        position: absolute;
        top: 5px;
        left: 5px;
      }
    }
  }
}
</style>
