<template>
  <section>
    <div class="card my-11 rounded">
      <a class="card-img cursor-pointer" @click="openProduct(product.id)">
        <img class="w-full" :src="product.image" alt="" />
      </a>

      <div class="card-body text-center p-2">
        <div class="product-name">
          <router-link to="#">{{ product.title }}</router-link>
        </div>
        <div class="py-4">
          Price:<span>{{ product.price }} $</span>
        </div>
        <div class="rate flex justify-center">
          <span>
            <font-awesome-icon
              :icon="[product.rating.rate > 1 ? 'fa' : 'far', 'star']"
              class="text-secondary"
            ></font-awesome-icon>
          </span>
          <span>
            <font-awesome-icon
              class="text-secondary"
              :icon="[product.rating.rate > 2 ? 'fa' : 'far', 'star']"
            ></font-awesome-icon>
          </span>
          <span>
            <font-awesome-icon
              class="text-secondary"
              :icon="[product.rating.rate > 3 ? 'fa' : 'far', 'star']"
            ></font-awesome-icon>
          </span>
          <span>
            <font-awesome-icon
              class="text-secondary"
              :icon="[product.rating.rate > 4 ? 'fa' : 'far', 'star']"
            ></font-awesome-icon>
          </span>
          <span>
            <font-awesome-icon
              class="text-secondary"
              :icon="[product.rating.rate > 4 ? 'fa' : 'far', 'star']"
            ></font-awesome-icon>
          </span>
          <span class="px-2 text-sm">{{ product.rating.rate }}</span>
        </div>
      </div>
      <button
        @click="addToCart(product)"
        class="bg-secondary rounded-t-2xl text-white font-bold py-4 px-5"
      >
        Add to card
      </button>
    </div>
  </section>
</template>

<script>
export default {
  props: ["product"],
  data() {
    return {};
  },
  methods: {
    openProduct(id) {
      this.$store.commit("getProductId", id);
      this.$store.commit("changeModalActive", true);
    },
    addToCart() {
      this.$store.commit("addToCart", this.product);
      this.$toasted.show(`Added SuccessFully`, {
        type: "success",
        duration: 3000,
        position: "bottom-center",
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.card {
  box-shadow: 0px 3px 10px #00000029;
  max-width: 400px;
  .card-img {
    img {
      min-height: 300px;
      max-height: 300px;
    }
  }
  .card-body {
    padding: 10px 20px;
    .product-name {
      overflow: hidden;
      text-overflow: ellipsis;
      word-break: break-word;
      white-space: nowrap;
    }
  }
}
</style>
