<template>
  <div class="container mx-auto">
    <table class="card w-full">
      <thead class="card-header bg-secondary text-lg font-bold">
        <tr class="">
          <td class="w-6/12 py-2"></td>
          <td class="w-2/12 py-2">qty</td>
          <td class="w-2/12 py-2">price</td>
          <td class="w-2/12 py-2">Total</td>
        </tr>
      </thead>
      <tbody class="card-body bg-primary border-b font-bold">
        <tr class="mt-4" v-for="product in products" :key="product.id">
          <td class="flex justify-center flex-wrap items-center">
            <div class="w-full sm:w-1/2 flex justify-center">
              <img
                style="width: 100px; height: 100px"
                :src="product.image"
                alt=""
              />
            </div>
            <p class="w-full sm:w-1/2 text-xs sm:text-sm">
              {{ product.title }}
            </p>
          </td>
          <td>
            <div class="flex justify-center">
              <button @click="removeFromCart(product)" class="btn">-</button>
              <p class="btn">{{ product.quantity }}</p>
              <button @click="addToCart(product)" class="btn">+</button>
            </div>
          </td>
          <td class="text-xs sm:text-sm">{{ product.price.toFixed(2) }}$</td>
          <td class="text-xs sm:text-sm">
            {{ (product.price * product.quantity).toFixed(2) }}$
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  mounted() {
    console.log(this.product);
  },
  methods: {
    addToCart(product) {
      this.$store.commit("addToCart", product);
    },
    removeFromCart(product) {
      this.$store.commit("removeFromCart", product);
    },
  },

  computed: {
    totalItemPrice() {
      return this.product.quantity * this.product.price;
    },
    products() {
      return this.$store.getters.cartItems;
    },
  },
};
</script>

