<template>
  <section class="bg-black">
    <div
      v-if="categories"
      class="category relative flex items-center justify-between"
    >
      <div class="container mx-auto flex justify-between items-center">
        <router-link
          class="font-bold text-2xl text-white"
          :to="`/category/all`"
        >
          All Categories
        </router-link>
        <ul class="hidden justify-center md:flex">
          <li
            class="py-4 px-4"
            v-for="(category, index) in categories"
            :key="index"
          >
            <router-link
              class="link text-lg lg:text-xl font-bold capitalize text-secondary"
              :to="`/category/${category}`"
              >{{ category }}</router-link
            >
          </li>
        </ul>
        <button class="openMenu md:hidden block" @click="menuOpen = !menuOpen">
          <font-awesome-icon
            :icon="['fas', 'bars']"
            class="text-white text-3xl"
          ></font-awesome-icon>
        </button>
        <transition name="menuOpen">
          <div class="mobileMenu" v-show="menuOpen">
            <ul class="">
              <li
                class="py-4 px-4"
                v-for="(category, index) in categories"
                :key="index"
              >
                <router-link
                  class="link text-lg lg:text-xl font-bold capitalize text-secondary"
                  :to="`/category/${category}`"
                  >{{ category }}</router-link
                >
              </li>
            </ul>
          </div>
        </transition>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      categories: null,
      menuOpen: false,
    };
  },
  mounted() {
    this.fetchCategories();
  },
  methods: {
    fetchCategories() {
      this.axios.get("products/categories").then((data) => {
        this.categories = data.data;
      });
    },
  },
};
</script>

