import Vue from "vue";
import Vuex from "vuex";
import firebase from "firebase/app";
import "firebase/auth";
import db from "../firebase/firebaseInit";
Vue.use(Vuex);

function updateLocaleStorage(cart) {
  localStorage.setItem("cart", JSON.stringify(cart));
}

export default new Vuex.Store({
  state: {
    user: null,
    firstName: "",
    lastName: "",
    userId: "",
    email: "",
    state: "",
    city: "",
    streetName: "",
    profileInitials: "",
    modalActive: false,
    productId: "",
    // cartTest: [],
    cart: JSON.parse(localStorage.getItem("cart")) || [],
  },

  mutations: {
    changeModalActive(state, payload) {
      state.modalActive = payload;
    },
    getProductId(state, payload) {
      state.productId = payload;
    },
    addToCart(state, product) {
      let item = state.cart.find((i) => i.id === product.id);
      if (item) {
        item.quantity++;
      } else {
        state.cart.push({ ...product, quantity: 1 });
      }
      updateLocaleStorage(state.cart);
    },
    removeFromCart(state, product) {
      console.log(product);
      let items = state.cart.find((i) => i.id === product.id);
      if (items) {
        if (items.quantity > 1) {
          items.quantity--;
        } else {
          state.cart = state.cart.filter((i) => i.id !== product.id);
        }
      }
      updateLocaleStorage(state.cart);
    },
    setProfileInfo(state, doc) {
      state.userId = doc.id;
      state.email = doc.data().email;
      state.firstName = doc.data().firstname;
      state.lastName = doc.data().lastname;
      state.city = doc.data().city;
      state.streetName = doc.data().streetName;
      state.state = doc.data().state;
      // state.cartTest = doc.data().cart;
    },
    setProfileInitials(state) {
      state.profileInitials =
        state.firstName.match(/(\b\S)?/g).join("") +
        state.lastName.match(/(\b\S)?/g).join("");
    },
    changeFirstName(state, payload) {
      state.firstName = payload;
    },
    changeLastName(state, payload) {
      state.lastName = payload;
    },
    changeState(state, payload) {
      state.state = payload;
    },
    changeCity(state, payload) {
      state.city = payload;
    },
    changeStreetName(state, payload) {
      state.streetName = payload;
    },
    updateUser(state, payload) {
      state.user = payload;
    },
  },
  getters: {
    productQuantity: (state) => (product) => {
      const items = state.cart.find((i) => i.id === product.id);
      if (items) return items.quantity;
      else return null;
    },
    cartItems: (state) => {
      return state.cart;
    },
    totalPrice: (state) => {
      return state.cart.reduce((a, b) => a + b.price * b.quantity, 0);
    },
    getCartCount: (state) => {
      return state.cart.length;
    },
    isAuthenticated: (state) => !!state.user,
  },
  actions: {
    async getCurrentUser({ commit }) {
      const database = db
        .collection("users")
        .doc(firebase.auth().currentUser.uid);
      const result = await database.get();
      commit("setProfileInfo", result);
      commit("setProfileInitials");
    },
    async updateUserSettings({ commit, state }) {
      const database = await db.collection("users").doc(state.userId);
      await database.update({
        firstname: state.firstName,
        lastname: state.lastName,
        city: state.city,
        state: state.state,
        streetName: state.streetName,
      });
      commit("setProfileInitials");
    },
  },
  modules: {},
});
