<template>
  <section class="form-wrapper flex justify-center items-center h-half-screen">
    <loading v-if="loading" />
    <modal v-if="activeModal" :errMsg="errMsg" v-on:close-modal="closeModal" />
    <form class="">
      <div>
        <input
          type="email"
          class="w-full border-b border-black mb-11 focus:outline-none p-4"
          placeholder="Enter your Email"
          v-model="email"
        />
      </div>
      <button
        class="px-4 py-2 bg-secondary rounded-lg font-bold text-xl"
        @click.prevent="resetPassword"
      >
        Reset Password
      </button>
    </form>
  </section>
</template>

<script>
import Modal from "../components/Modal.vue";
import firebase from "firebase/app";
import "firebase/auth";
import Loading from "../components/Loading.vue";
export default {
  components: { Modal, Loading },
  data() {
    return {
      errMsg: "",
      email: "",
      activeModal: null,
      loading: null,
    };
  },
  methods: {
    closeModal() {
      this.activeModal = !this.activeModal;
    },
    resetPassword() {
      this.loading = true;
      firebase
        .auth()
        .sendPasswordResetEmail(this.email)
        .then(() => {
          this.loading = false;
          this.activeModal = true;
          this.errMsg =
            "if you have account check your email to reset password";
        })
        .catch((err) => {
          this.loading = false;
          this.activeModal = true;
          this.errMsg = err.message;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.form-wrapper {
  height: calc(100vh - 180px);
  form {
    width: 100%;
    max-width: 350px;
  }
}
</style>
